/* Arrows */

.slick-prev,
.slick-next {
    position: absolute;
    display: block;
    height: 20px;
    width: 20px;
    line-height: 0px;
    font-size: 0px;
    cursor: pointer;
    background: transparent;
    color: transparent;
    top: 50%;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);
    padding: 0;
    border: none;
    outline: none;
    &:hover, &:focus {
        outline: none;
        background: transparent;
        color: transparent;
        &:before {
            opacity: $slick-opacity-on-hover;
        }
    }
    &.slick-disabled:before {
        opacity: $slick-opacity-not-active;
    }
    &:before {
        //font-family: $slick-font-family;
        font-family:'FontAwesome';
        font-size: 24px;
        line-height: 1;
        //color: $slick-arrow-color;
        opacity: $slick-opacity-default;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
}

.slick-prev {
    left: -25px;
    [dir="rtl"] & {
        left: auto;
        right: -25px;
    }
    &:before {
        //content: $slick-prev-character;
		content: "\f104";
		
        [dir="rtl"] & {
			content: "\f104"
        }
    }
} 

.slick-next {
    right: -25px;
    [dir="rtl"] & {
        left: -25px;
        right: auto;
    }
    &:before {
        content: "\f105";
        [dir="rtl"] & {
            content: "\f105";
        }
    }
}

/* Dots */

.slick-dotted.slick-slider {
    margin-bottom:0;
}

.slick-dots {
    position:absolute;
    bottom:-80px;
    list-style: none;
    display: block;
    text-align: center;
    padding: 0;
    margin: 0;
    width: 100%;
	z-index:100;
    li {
        position: relative;
        display: inline-block;
        height:10px;
        width:10px;
        padding:0;
        margin:0 10px;
        cursor: pointer;
		background:#fff;
		text-indent:-9999px;
		border-radius:8px;
		opacity: 0.3;
		
        &.slick-active {
            opacity:1;
        }
    }
}
